/* Usage verifed */
$font-size--2xs: 0.75em; // 12px
$font-size--xs: 0.8125em; // 13px
$font-size--small: 0.875em; // 14px
$font-size--medium: 0.9375em; // 15px
$font-size--standard: 1em; // 16px
$font-size--large: 1.125em; // 18px
$font-size--xl: 1.25em; // 20px
$font-size--2xl: 1.375em; // 22px
$font-size--3xl: 1.5em; // 24px

$border-thin--standard: 1px solid;
$border-thin--medium: 2px solid;
$border-thin--large: 3px solid;
$border-thin--xl: 4px solid;

$border-radius--standard: 4px;
$border-radius--medium: 6px;
$border-radius--large: 8px;

$box-shadow--standard: 0.0625em 0.125em 0.375em; // 1px 2px 6px

$icon--font-family: "feather";

// Line height
$line-height--standard: normal; // Anvnds nr vi vill terstlla line-height till standard
$line-height--small: 1; // 16px
$line-height--medium: 1.125; // 18px
$line-height--large: 1.25; // 20px
$line-height--xl: 1.375; // 22px
$line-height--2xl: 1.5; // 24px
$line-height--3xl: 1.625em; // 26px
$line-height--4xl: 1.75em; // 28px

// Spacing
$space-0: 0;
$space-px: 1px;
$space-1: 0.25rem;
$space-2: 0.5rem;
$space-3: 0.75rem;
$space-4: 1rem;
$space-5: 1.25rem;
$space-6: 1.5rem;
$space-8: 2rem;
$space-10: 2.5rem;
$space-12: 3rem;
$space-16: 4rem;
$space-20: 5rem;
$space-24: 6rem;
$space-32: 8rem;
$space-40: 10rem;
$space-48: 12rem;
$space-56: 14rem;
$space-64: 16rem;
/* End of usage verifed */

// ********
// SITE ***
// ********

$site: all;

// *******************
// frn _base.scss ***
// *******************



// Breakpoint definition
$breakpoint-sm: 36em; // Small devices - 576px and up
$breakpoint-md: 48em; // Medium devices - 768px and up
$breakpoint-lg: 62em; // Large devices - 992px and up
$breakpoint-xl: 75em; // Extra large devices - 1200px and up

$breakpoints: (
    xs: 0,
    sm: $breakpoint-sm,
    md: $breakpoint-md,
    lg: $breakpoint-lg,
    xl: $breakpoint-xl
) !default;

// Widths
$width-1: 24rem;
$width-2: 32rem;
$width-3: 48rem;
$width-4: 64rem;

/* ****************** */
/*   Color settings   */
/* ****************** */

// Descriptive Base Colors
$white: #fff;
$black: #000;

// Grey colors
$grey-lightest: #f4f4f4;
$grey-lighter: #e4e4e4;
$grey-light: #d1d1d1;
$grey-base: #b1b1b1;
$grey-dark: #7e7e7e;
$grey-darker: #575757;
$grey-darkest: #2f2f2f;

// Base1 colors
$base1-light: #b2c8de;
$base1: #004890;
$base1-dark: #003265;

// Base2 colors
$base2-lighter: #f5f8fb;
$base2-light: #d0e3f3;
$base2: #61a2d8;
$base2-dark: #447197;

// Base3 colors
$base3-light: #c3dcc1;
$base3: #378a30;
$base3-dark: #266122;

// Complementary1 colors
$complementary1-light: #edf1c9;
$complementary1: #c3d04c;
$complementary1-dark: #899235;

// Complementary2 colors
$complementary2-light: #fee7b9;
$complementary2: #fcaf15;
$complementary2-dark: #b07a0f;

// Complementary3 colors
$complementary3-light: #e3b7be;
$complementary3: #a01026;
$complementary3-dark: #700b1b;

// Warning colors
$warning-light: #fcc8c8;
$warning: #c82828;
$warning-dark: #941e1e;

// Vuxhalland colors
//$vuxhalland: #e99579;
$vuxhalland2: #d07d88;

// Link colors
$link-visited: #551a8b;

// Color Usage
$color-primary: $base1;
$color-secondary: $base3;
$color-shadow: rgba($black, 0.35);
$color-overlay: rgba($black, 0.30);
$yellow-background: #fef9ee;

// *************************
// Typography settings   ***
// *************************

// Font
// TODO: Body tag gets font-family: proxima in core/_base.scss. Move it to a setting.
$sans-font-family: Arial, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Helvetica, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
$code-font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier,
    monospace;

// Spacing
$letter-spacing: 1;
$caps-letter-spacing: 0.2em;

// Weight
$bold-font-weight: bold;

// Type-scale
$h1: 2rem;
$h2: 1.5rem;
$h3: 1.25rem;
$h4: 1rem;
$h5: 0.875rem;
$h6: 0.75rem;

// Typography small
$h1-small: 1.5em;//em(24);
$h2-small: 1.375em;//em(22);
$h3-small: 1.25em;//em(20);
$h4-small: 1.125em;//em(18);
$h5-small: 1em;//em(16);
$h6-small: 0.875em;//em(14);
$p-small: 0.8125em;//em(13);

// Typography medium
$h1-medium: 2em;//em(32);
$h2-medium: 1.625em;//em(26);
$h3-medium: 1.375em;//em(22);
$h4-medium: 1.25em;//em(20);
$h5-medium: 1.125em;//em(18);
$h6-medium: 1em;//em(16);
$p-medium: 0.9375em;//em(15);

// Typography large
$h1-large: 2.5em;//em(40);
$h2-large: 1.75em;//em(28);
$h3-large: 1.5em;//em(24);
$h4-large: 1.375em;//em(22);
$h5-large: 1.25em;//em(20);
$h6-large: 1.125em;//em(18);
$p-large: 1em;//em(16);

// Heading size
$heading-line-height-small: 2.1875em;//em(35);
$heading-line-height-medium-large: 1.875em;//em(30);

// Font-size
$font-size-small: 1em;//em(16);
$font-size-medium: 1em;//em(16);
$font-size-large: 1em;//em(16);

/* ********************** */
/*   Container settings   */
/* ********************** */

$page__container--min-width: 18em; //20em - 320px - iPhone 5
$page__container--max-width: 90em; //1440px

$page__container--padding-x: 0.7em; //11.2px - For small telephone
$page__container--small-padding-x: 1em; //16px
$page__container--medium-padding-x: 1.875em; //30px
$page__container--large-padding-x: 3.75em; //60px
$page__container--xlarge-padding-x: 5em; //80px

/* ********************** */
/*   Grid settings        */
/* ********************** */

$grid-gutter-width: 1.4em;
$grid-gutter-half-width: $grid-gutter-width/2;

/* ********************** */
/*   Section gutters      */
/* ********************** */

$section-gutters: 0.7em; //11.2px x 2 = 22.4px
$section-gutters-large: 0.9375em; //15px x 2 = 30px
$section-gutters-xlarge: 1.875em; //30px x 2 = 60px

